import { LoadingProvider } from './../../providers/loading/loading';
import { RestProvider } from './../../providers/rest/rest';
import { AuthProvider } from './../../providers/auth/auth';
import { GlobalProvider } from './../../providers/global/global';
import { Component } from '@angular/core';
import { NavController, PopoverController, AlertController, IonicPage, Platform } from 'ionic-angular';
import { ModalController } from 'ionic-angular';
import { OneSignal } from '@ionic-native/onesignal';

@IonicPage()
@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  constructor(
    public navCtrl: NavController, 
    public popoverCtrl: PopoverController, 
    public global: GlobalProvider, 
    public modal: ModalController,
    public alertCtrl: AlertController,
    public auth: AuthProvider,
    public rest:  RestProvider,
    public loading: LoadingProvider,
    public oneSignal: OneSignal,
    public platform: Platform,
  ) {
    this.global.getUserInfo();
  }

  ionViewDidLoad(){

    if (!this.platform.is('cordova')) {
      return;
    }
    else {

      this.oneSignal.getIds().then(
        (scs) => {
          console.log('onesignal user id: ' + scs.userId);
          
          this.rest.storeOneSignalUserId(scs.userId).then(
            (scs) => {
              console.log('thanh cong roi');
              
            },
            (err) => {
              console.log(err);
              
            }
          )
        },
        (err) => {
          console.log(err);
          
        }
      )
    }
  }

  ionViewWillEnter(){
    if (!this.auth.authenticated()) {
      this.auth.logout();
      this.navCtrl.setRoot('LoginPage');
      return;
    }
    this.checkNotification();
  }

  checkNotification() {
    this.rest.countUnreadNotification().then(
      (scs:any) =>{
        this.global.unreadNotificationCount = scs.count;
      },
      (err) => {
        console.log(err);
        
      }
    )
  }

  presentNotifications(myEvent) {
    // let popover = this.popoverCtrl.create('NotificationPage');
    // popover.present({
    //   ev: myEvent
    // });
    this.navCtrl.push('NotificationPage');
  }

  logout() {
    this.auth.logout();
    this.navCtrl.setRoot('LoginPage');
  }
  
  openWalletPage() {
    this.navCtrl.push('WalletPage');
  }

  openCartHistoryPage() {
    this.navCtrl.push('CartHistoryPage');
  }

  openSuggestProductPage() {
    this.navCtrl.push('SuggestProductPage');
  }
  
  openColorMixPage(){
    this.navCtrl.push('ColorMixPage');
  }

  presentLogoutConfirm() {
    let alert = this.alertCtrl.create({
      title: 'Xác nhận',
      message: 'Bạn có chắc chắn muốn đăng xuất??',
      cssClass: 'alert-logout',
      buttons: [
        {
          text: 'Hủy',
          role: 'cancel',
          handler: () => {
            console.log('Cancel');
          }
        },
        {
          text: 'Đăng xuất',
          role: 'ok',
          handler: () => {
            this.logout();
          }
        }
      ]
    });
    alert.present();
  }

}



